.ao-testimonials__veil {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 50%;
	background: linear-gradient(
			to bottom,
			rgba(255, 255, 255, 0) 0%,
			rgba(255, 255, 255, 1) 100%
	);
	z-index: 2;
	transition: all 0.6s ease-in-out;
}

.ao-testimonials__veil,
.ao-testimonials__load-more {
	display: block;
}


.ao-testimonials__load-more {
	position: absolute;
	bottom: 1rem;
	left: 50%;
	transform: translateX(-50%);
	z-index: 3;
	background-color: white;
	color: #dfa01f;
	border: 2px solid #dfa01f;
	padding: 1.5rem 5rem;
	font-size: 1.25rem;
	font-weight: 700;
	cursor: pointer;
	transition: background-color 0.3s ease;
	line-height: 1;
	text-transform: uppercase;
	border-radius: 300px;
	margin-bottom: 2rem;
}



@media (min-width: 1024px) {

	.ao-testimonials__load-more {
		color: #3c4b5f;
		border: 2px solid #3c4b5f;
	}

	.ao-testimonials__load-more:hover {
		background-color: #dfa01f;
		color: white;
		border: 2px solid #dfa01f;
	}
}

.ao-testimonials--expanded .ao-testimonials__veil {
	height: 0;
	visibility: hidden;
	opacity: 0;
}

.ao-testimonials--expanded .ao-testimonials__load-more {
	display: none;
}

@media (max-width: 1023px) {
	.ao-testimonials__load-more{
		font-size: 16px;
		line-height: 100%;
		padding: 16px;
		width: calc(100% - 46px);
	}
}